<template>
  <v-container fluid grid-list-md>
    <v-layout  row  >
      <v-flex xs12 sm6 offset-sm3>
        <v-card class="elevation-12">
          <v-toolbar dark color="teal darken-2">
            <v-toolbar-title>แก้ไขข้อมูลกลุ่ม</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form lazy-validation>
              <v-text-field 
                prepend-icon="G"
                v-model="groupId"
              ></v-text-field>
              <v-text-field disabled
                label="ชื่อกลุ่ม"
                v-model="groupName"
              ></v-text-field>
              <v-text-field
                label="จำนวนสมาชิก"
                v-model="groupMember"
              ></v-text-field>
              <v-text-field
                label="ผู้ประสานงาน"
                v-model="coordinator"
              ></v-text-field>
              <v-text-field
                label="เบอร์โทรผู้ประสานงาน"
                v-model="coordinatorTel"
              ></v-text-field>
              <v-text-field
                label="ยอดคำภาวนา"
                v-model="yodSamma"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn flat color="red lighten-1" to="/group">back</v-btn>
            <v-spacer />
            <v-btn color="green darken-4" round dark large  @click="updateEmployee()"
              >submit
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import db from "@/components/firebaseInit";
  export default {
    name: "edit-group",
    data() {
      return {
      valid: false,
      groupId: '',
      groupName: '',
      groupMember: '',
      coordinator: '',
      coordinatorTel: '',
      yodSamma: ''
      };
    },
    beforeRouteEnter(to, from, next) {
      db.collection("yodByUser")
        .where("groupName", "==", to.params.groupName)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.groupId = doc.data().groupId;
              vm.groupName = doc.data().groupName;
              vm.groupMember = doc.data().groupMember;
              vm.coordinator = doc.data().coordinator;
              vm.coordinatorTel = doc.data().coordinatorTel;
              vm.yodSamma = doc.data().yodSamma;
            });
          });
        });
    },
    watch: {
      $route: "fetchData"
    },
    methods: {
      fetchData() {
        db.collection("yodByUser")
          .where("groupName", "==", this.$route.params.groupName)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.groupId = doc.data().groupId;
              this.groupName = doc.data().groupName;
              this.groupMember = doc.data().groupMember;
              this.coordinator = doc.data().coordinator;
              this.coordinatorTel = doc.data().coordinatorTel;
              this.yodSamma = doc.data().yodSamma;
            });
          });
      },
      updateEmployee () {
        db.collection("yodByUser")
          .where("groupName", "==", this.$route.params.groupName)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                groupId: this.groupId,
                groupName: this.groupName,
                groupMember: this.groupMember,
                coordinator: this.coordinator,
                coordinatorTel: this.coordinatorTel,
                yodSamma: this.yodSamma
              })
              .then(() => {
                this.$router.push('/group')
              })
            });
          });
      }
    }
  };
</script>


